
/* CSS for the navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  padding: 5px;
  background-color: white;
  color: white;
  padding-left: 30px;
  border: 1px solid #f8f3f3;
  background-color: rgba(246, 239, 239, 0.885);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
}

.navbar .left-section button {
  background-color: #f4f6f7;
  color: rgb(19, 17, 17);
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 2px;
  font-size: 8px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
  border: 1px solid #c9c6c6;
}


/* CSS for the modal */
.modal {
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 8px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(245, 234, 234, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.options-box{
  width: 100%;
  font-size: 10px;
  padding: 0px;
  text-align: left;
  
}
.options-box select{
font-size: 8px;
}
.group-option{
  width: 100%;
  padding: 5px;
  flex: 1;
  display: flex;
  flex-direction: row;
}

.sort-option{
  width: 100%;
  padding: 5px;
  flex: 1;
  display: flex;
  flex-direction: row;

}

.padding-option{
  padding-left: 20px;
