}

/* Modal styles */
.options-box {
  display: flex;
  flex-direction: column;
}

/* Kanban board styles */
.kanban-board {
  display: flex;
  flex-wrap: nowrap;
  width: auto; 
  height: calc(100vh - 64px); 
  padding: 10px; 
}

.status-container {
  display: flex;
  flex-direction: row;
  margin-right: auto;
  min-width: inherit;
  flex-grow: 1; 
  max-width: 150px;
  width: 10px;
  
}

.user-container {
  display: flex;
  flex-direction: row;
  margin-right: 10px;
  min-width: auto;
}



.priority-container {
  display: flex;
  flex-direction: row;
  min-width: auto;;
}

/* Ticket styles */
.ticket {
  width: 100%; 
  margin: 5px;
  border: 1px solid #5d5a5a; 
  padding: 5px; 
  background-color: #fff; 
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.ticket-info {
  display: flex;
  justify-content: space-between;
}

.user {
  display: flex;
  justify-content: space-between;
}


